.App {
  text-align: center;
}
body {
  background-color: #f5f5f5;
}

.gameButton {
  height: 200px;
  width: 198px;
  border: 1px solid black;
}

.red {
  background-color: red;
  float: right;
  border-top-right-radius: 100%;
}
.green {
  background-color: green;
  float: left;
  border-top-left-radius: 100%;
}
.yellow {
  background-color: yellow;
  float: left;
  border-bottom-left-radius: 100%;
}
.blue {
  background-color: blue;
  float: right;
  border-bottom-right-radius: 100%;
}

.g-light {
  background-color: #80ff80;
}

.r-light {
  background-color: #ff8080;
}

.b-light {
  background-color: #b3b3ff;
}

.y-light {
  background-color: #ffffb3;
}

.colors-circle {
  width: 400px;
}

.grid-container {
  display: grid;
  justify-items: center;
  height: 100vh;
  position: relative;
  top: 170px;
}
.display-container {
  border-radius: 50%;
  background-color: white;
  border: 1px solid black;
  width: 190px;
  height: 190px;
  z-index: 1;
  position: relative;
  bottom: 340px;
  display: grid;
  justify-items: center;
  align-items: center;
}

.start-button {
  background-color: lightgray;
  padding: 5px;
  border: 0.5px solid black;
  border-radius: 15%;
  width: 35px;
  text-align: center;
}
.strict-button {
  background-color: lightgray;
  padding: 5px;
  border: 0.5px solid black;
  border-radius: 15%;
  width: 35px;
  text-align: center;
}
.strictmode {
  background-color: lightgreen;
}
